.single {
    flex: 9;
    display: flex;
}

.singlePostWrapper {
    padding: 20px;
    padding-right: 0;
}

.singlePostImg {
    width: 100%;
    height: 300px;
    border-radius: 5px;
    object-fit: cover;
}

.singlePostTitle {
    font-family: "Josefin Sans", Arial, Helvetica, sans-serif;
    font-weight: 900;
    text-align: center;
    margin-top: 10px;
    /* margin-left: 40%; */
    font-size: 28px;
    display: flex;
}

.singlePostEdit {
    left: 5%;
    font-size: 28px;
    z-index: 0;
}

.singlePostIcon {
    padding: 8px;
    margin-left: 15%;
    cursor: pointer;
    transition: all 0.4s ease 0s;
}

.singlePostIcon:hover {
    transform: scale(1.5);
}

.singlePostIcon:first-child {
    color: teal;
}

.singlePostIcon:last-child {
    color: tomato;
}

.singlePostInfo {
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    font-size: 16px;
    color: #be9656;
    font-family: "Varela Round", Arial, Helvetica, sans-serif;
}

.singlePostAuthor {
    margin-left: 5px;
}

.singlePostDesc {
    color: #666;
    font-size: 18px;
    line-height: 25px;
}

.singlePostDesc::first-letter {
    margin-left: 20px;
    font-size: 30px;
    font-weight: 600;
}
